import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'
import { useNavigate } from 'react-router'

import { COMMON_MESSAGE, MANAGE_RECRUITMENT_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/club'
import type { DateProps } from 'src/models/date'
import type { RecruitId, RecruitList } from 'src/models/recruit'

import type { AddRecruitRequestData, GetRecruitResponseData, PostRecruitResponse } from 'src/responses/recruit'
import type { SearchRecruitsResponseBody } from 'src/responses/search'

interface GetRecruitParameters {
  clubId: ClubId
  recruitId?: RecruitId
  token: string
}

const getRecruitsAPICall =
  ({ clubId, recruitId, token }: GetRecruitParameters) =>
  async () => {
    const url = recruitId
      ? `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}`
      : `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit`
    const response = await axios.get<GetRecruitResponseData>(url, {
      headers: { Authorization: token }
    })
    return response.data
  }

export const useGetRecruits = (clubId: ClubId, recruitId?: RecruitId) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetRecruitResponseData>(['recruits'], getRecruitsAPICall({ clubId, recruitId, token }), {
    onError: error => {
      if (error instanceof AxiosError) {
        return
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    },
    retry: 0
  })
}

interface PutRecruitParams {
  clubId: ClubId
  data: AddRecruitRequestData
  recruitId: number
}

const putRecruitAPICall = async ({ clubId, data, recruitId }: PutRecruitParams, token: string) => {
  await axios.put<null, AxiosResponse<null>, AddRecruitRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

export const useEditRecruit = (): UseMutationResult<void, unknown, PutRecruitParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PutRecruitParams) => putRecruitAPICall(values, token)

  return useMutation('putRecruit', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.UPDATE_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('recruits')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

interface PostRecruitParams {
  clubId: ClubId
  data: AddRecruitRequestData
  date: DateProps
}

const postRecruitAPICall = async ({ clubId, data, date }: PostRecruitParams, token: string) => {
  const response = await axios.post<null, { data: PostRecruitResponse }, AddRecruitRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit?semester=${date.semester}&year=${date.year}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
  return response.data.recruitId
}

export const useCreateRecruit = () => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  return useMutation(
    'postRecruit',
    async (values: PostRecruitParams) => {
      const response = await postRecruitAPICall(values, token)
      return response
    },
    {
      onSuccess: () => {
        enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
        queryClient.invalidateQueries('recruits')
      },
      onError: error => {
        if (error instanceof AxiosError) {
          const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
          enqueueSnackbar(errorMessage, { variant: 'error' })
        } else {
          enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
        }
      }
    }
  )
}

export const getRecruitsByParams = (apiParams: URLSearchParams, token: string) => async () => {
  const response = await axios.get<SearchRecruitsResponseBody>(`${process.env.REACT_APP_APIHOST}/search/recruits`, {
    params: apiParams,
    headers: { Authorization: token }
  })
  return response.data
}

export const useGetRecruitsByParam = (searchParams: URLSearchParams) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const response = useQuery<SearchRecruitsResponseBody>(
    ['recruitsByParam', searchParams.toString()],
    getRecruitsByParams(searchParams, token),
    {
      onError: error => {
        if (!(error instanceof AxiosError)) {
          enqueueSnackbar('서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
            variant: 'error'
          })
        }
      }
    }
  )
  return response
}

interface GetRecruitListParameters {
  clubId: ClubId
  token: string
}

export const getRecruitListAPICall = async ({ clubId, token }: GetRecruitListParameters) => {
  const response = await axios.get<RecruitList[]>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/list`, {
    headers: { Authorization: token }
  })
  return response.data
}

export const useGetRecruitList = (clubId: number) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const recruitList = useQuery(['recruitList', clubId], () => getRecruitListAPICall({ clubId, token }), {
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(COMMON_MESSAGE.LOAD_FAIL, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  })
  return recruitList
}

export interface DeleteParameters {
  clubId: number
  token: string
  recruitIds: RecruitId[]
}

export const deleteRecruitAPICall = async ({ clubId, token, recruitIds }: DeleteParameters) => {
  recruitIds.forEach(async recruitId => {
    await axios.delete<null, AxiosResponse<null>, null>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}`,
      {
        headers: { Authorization: token }
      }
    )
  })
}

export const useDeleteRecruit = (clubId: number, recruitIds: number[], successMessage: string) => {
  const { enqueueSnackbar } = useSnackbar()

  const token = useAuthHeader()()
  const queryClient = useQueryClient()
  const mutationFn = () => deleteRecruitAPICall({ clubId, token, recruitIds })
  return useMutation(mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(successMessage, { variant: 'success' })
    },
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    },
    onSettled: () => {
      queryClient.invalidateQueries('recruitList')
    }
  })
}

interface GetRecruitExistenceParams {
  clubId: ClubId
  date: DateProps
  token: string
}

export const getRecruitExistenceAPI = async ({ clubId, date, token }: GetRecruitExistenceParams) => {
  const response = await axios.get(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/check?semester=${date.semester}&year=${date.year}`,
    {
      headers: { Authorization: token }
    }
  )
  return response
}

export const useGetRecruitExistence = (clubId: number, date: DateProps, isSubmit: boolean) => {
  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const token = useAuthHeader()()

  const response = useQuery(['recruitExistence', clubId, date], () => getRecruitExistenceAPI({ clubId, date, token }), {
    onSuccess: () => {
      enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.RECRUIT_EXIST_MESSAGE, { variant: 'warning' })
    },
    onError: err => {
      if (err instanceof AxiosError) {
        if (err.response?.status === 400) {
          navigate(`/manage/${clubId}/recruit/register`)
        } else {
          enqueueSnackbar(COMMON_MESSAGE.LOAD_FAIL, { variant: 'error' })
        }
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    },
    enabled: !!isSubmit
  })
  return response
}
