import { useSnackbar } from 'notistack'
import React, { useEffect, useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import { Box, Button, Grid, Paper, type SelectChangeEvent, Typography, useMediaQuery } from '@mui/material'
import { styled, useTheme } from '@mui/material/styles'

import { CLUB_APPLY_MESSAGE, COMMON_MESSAGE } from 'src/data/messages'

import { formatLikePhoneNumber } from 'src/utils/formatLikePhoneNumber '

import type { ApplyForm } from 'src/models/apply'
import type { ClubMemberForAuth } from 'src/models/clubMember'

import ConfirmDialog from 'src/components/ConfirmDialog'

import ApplyField from 'src/content/club/Intro/Apply/ApplyField'

import { useApplyForm } from 'src/hooks/api/apply'
import { useGetQuestions } from 'src/hooks/api/forms'
import { useGetRecruits } from 'src/hooks/api/recruit'

const today = new Date()

const Apply: React.FC = () => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()

  const { id, recruitId } = useParams()
  const clubId = Number(id)
  const recruitIdNumber = Number(recruitId)

  const navigate = useNavigate()
  const authUser = useAuthUser()

  const { data: recruit, isSuccess: isRecruitSuccess, isLoading } = useGetRecruits(clubId)
  const { data: questions } = useGetQuestions(clubId, recruitIdNumber)

  const [applyInfo, setApplyInfo] = useState<ApplyForm>({
    name: authUser()?.name,
    phoneNumber: authUser()?.phoneNumber,
    department: authUser()?.department,
    studentId: authUser()?.studentId,
    email: authUser()?.email,
    answers: questions
      ? questions.map(question => ({
          questionId: question.id,
          text: null,
          optionIndices: null
        }))
      : []
  })

  const [isShowDialog, setIsShowDialog] = useState(false)

  const applyForm = useApplyForm()

  const isRecruitingPeriod =
    isRecruitSuccess && recruit.startAt <= today.toISOString() && today.toISOString() <= recruit.endAt

  const userGrade = parseInt(authUser()?.studentId.slice(2, 4))

  const isConditionMet =
    isRecruitSuccess &&
    (recruit.allowedCollege ? recruit.allowedCollege === applyInfo.department.split(' ')[0] : true) &&
    (recruit.maxGrade ? parseInt(recruit.maxGrade) >= userGrade : true) &&
    (recruit.minGrade ? parseInt(recruit.minGrade) <= userGrade : true)

  useEffect(() => {
    // 이미 회원인경우 접근 차단
    const isMember = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId).length > 0
    if (!isLoading && isMember) {
      enqueueSnackbar('이미 가입된 동아리입니다.', { variant: 'error' })

      setTimeout(() => {
        navigate(-1)
      }, 1000)
    }

    if (!isLoading && !isRecruitSuccess) {
      setTimeout(() => {
        navigate(-1)
      }, 1000)
    }
  }, [isLoading, isRecruitSuccess])

  const handleChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent<string>
  ) => {
    const { name, value } = event.target
    setApplyInfo(prevValues => ({ ...prevValues, [name]: value }))

    if (event.target instanceof HTMLInputElement && event.target.name === 'name') {
      const name = event.target
      const trimmedName = name.value.trim()
      const regex = /^[a-zA-Z0-9ㄱ-ㅎㅏ-ㅣ가-힣 ]*$/
      const isValid = regex.test(name.value)

      if (trimmedName !== name.value) {
        name.setCustomValidity('이름 앞뒤에 공백문자를 포함할 수 없습니다.')
        return
      }
      if (!isValid) {
        name.setCustomValidity('이름에 특수문자를 포함할 수 없습니다.')
        return
      }
      name.setCustomValidity('')
    }
  }

  const handleCheckBoxAnswerChange: React.ChangeEventHandler<HTMLInputElement> = ({ target: { name, value } }) => {
    if (!applyInfo.answers) return

    setApplyInfo({
      ...applyInfo,
      answers: {
        ...applyInfo.answers,
        [name]: {
          ...applyInfo.answers[Number(name)],
          optionIndices: [value]
        }
      }
    })
  }

  const handleTextAnswerChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { name, value } }) => {
    if (!applyInfo.answers) return
    setApplyInfo({
      ...applyInfo,
      answers: {
        ...applyInfo.answers,
        [name]: {
          ...applyInfo.answers[Number(name)],
          text: value
        }
      }
    })
  }

  const handlePhoneNumberChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { value } }) => {
    setApplyInfo(prevState => ({ ...prevState, phoneNumber: formatLikePhoneNumber(value) }))
  }

  const handleApply = () => {
    if (isRecruitSuccess) {
      const body = Object.assign({}, applyInfo)
      applyForm.mutate({ clubId, recruitId: recruit.id, data: body })
    }
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = e => {
    e.preventDefault()

    if (isRecruitSuccess) {
      setIsShowDialog(true)
      return
    }
    enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'warning' })
  }

  useEffect(() => {
    if (isRecruitSuccess) {
      // 마감일 23시 59분 59초까지 지원할 수 있도록 하루 더함
      const endAtNextDay = new Date(recruit.endAt)
      endAtNextDay.setDate(endAtNextDay.getDate() + 1)

      if (!(today <= endAtNextDay)) {
        enqueueSnackbar('지금은 지원 기간이 아닙니다.', { variant: 'error' })
        navigate(-1)
      }
    }
  }, [isRecruitSuccess])

  return (
    <PageWrapper>
      <Helmet>
        <title>동아리 지원</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(8),
          padding: 3,
          flex: 1,
          maxWidth: '480px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          동아리 지원서
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <ApplyField
            applyInfo={applyInfo}
            questions={questions ?? []}
            isRecruitingPeriod={isRecruitingPeriod}
            handleChange={handleChange}
            handleTextAnswerChange={handleTextAnswerChange}
            handlePhoneNumberChange={handlePhoneNumberChange}
            handleCheckBoxAnswerChange={handleCheckBoxAnswerChange}
          />
          <Button
            type="submit"
            variant="contained"
            fullWidth
            sx={{
              '&.Mui-disabled': {
                backgroundColor: theme.palette.divider
              }
            }}
            disabled={!isRecruitingPeriod || !isConditionMet}
          >
            지원서 제출
          </Button>

          <Grid container pl={2} justifyContent={'center'} mt={1}>
            <Typography variant="body1" display="inline" color={'error'}>
              {!isConditionMet
                ? '* 모집 제한 자격이 충족되지 않습니다'
                : !isRecruitingPeriod && '* 답변 작성 및 제출은 모집 시작 이후에 활성화됩니다.'}
            </Typography>
          </Grid>
        </Box>
      </Paper>
      <ConfirmDialog
        open={isShowDialog}
        handleClose={() => setIsShowDialog(false)}
        handleSubmit={handleApply}
        confirmMessage={CLUB_APPLY_MESSAGE.APPLY_CONFIRM}
      />
    </PageWrapper>
  )
}

export default Apply

const PageWrapper = styled(Box)(
  () => `
        display: flex;
        justify-content: center;
        align-items: center;
    `
)
