import { useAuthUser } from 'react-auth-kit'

import type { SelectChangeEvent, TextFieldProps } from '@mui/material'
import {
  Box,
  Divider,
  FormControl,
  FormControlLabel,
  InputLabel,
  ListSubheader,
  MenuItem,
  TextField as MuiTextField,
  Radio,
  RadioGroup,
  Select,
  TextareaAutosize,
  Typography,
  useTheme
} from '@mui/material'

import departments from 'src/data/departments'
import { HELPER_TEXT_MESSAGE } from 'src/data/messages'

import type { ApplyForm } from 'src/models/apply'
import type { QuestionViews } from 'src/models/form'

const MAX_LENGTH_PER_ANSWER = 1000

interface ApplyFieldProps {
  applyInfo: ApplyForm
  isRecruitingPeriod: boolean
  questions: QuestionViews | null
  handleChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent<string>) => void
  handleTextAnswerChange: React.ChangeEventHandler<HTMLTextAreaElement>
  handlePhoneNumberChange: React.ChangeEventHandler<HTMLTextAreaElement>
  handleCheckBoxAnswerChange: (event: React.ChangeEvent<HTMLInputElement>, answerIdx: number) => void
}

const ApplyField: React.FC<ApplyFieldProps> = ({
  applyInfo,
  isRecruitingPeriod,
  questions,
  handleChange,
  handleTextAnswerChange,
  handlePhoneNumberChange,
  handleCheckBoxAnswerChange
}) => {
  const authUser = useAuthUser()
  const theme = useTheme()

  return (
    <>
      <TextField
        id="email"
        type="email"
        label="이메일"
        disabled
        value={applyInfo.email}
        inputProps={{ readOnly: true }}
      />
      <TextField
        id="student-id"
        type="text"
        label="학번"
        disabled
        value={applyInfo.studentId}
        inputProps={{ readOnly: true }}
      />
      <TextField
        id="name"
        name="name"
        type="text"
        label="이름"
        inputProps={{ maxLength: 50 }}
        value={applyInfo.name}
        onChange={handleChange}
      />
      <TextField
        id="phone-number"
        name="phoneNumber"
        type="tel"
        label="전화번호"
        inputProps={{ pattern: '010-[0-9]{4}-[0-9]{4}', maxLength: 13 }}
        helperText={HELPER_TEXT_MESSAGE.PHONE_NUMBER}
        value={applyInfo.phoneNumber}
        onChange={handlePhoneNumberChange}
      />
      <FormControl sx={{ margin: 1, width: '100%' }} size="small" required>
        <InputLabel htmlFor="department">학과/학부</InputLabel>
        <Select
          id="department"
          name="department"
          label="학과/학부"
          value={applyInfo.department}
          onChange={handleChange}
          fullWidth
        >
          {departments &&
            Object.keys(departments).map(college => [
              <ListSubheader sx={{ fontWeight: 'bold' }}>{college}</ListSubheader>,
              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
              // @ts-ignore
              departments[college] &&
                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore
                departments[college].map((depart: string) => [
                  <MenuItem value={college === depart ? depart : college + ' ' + depart}>{depart}</MenuItem>
                ])
            ])}
        </Select>
      </FormControl>

      {!authUser()?.phoneNumber && (
        <Box>
          <Typography variant="body1" display="inline">
            입력하신 정보는 동아리 관리를 위해 서버에 저장됩니다.
          </Typography>
        </Box>
      )}

      <Divider />

      {questions && questions.length !== 0 && (
        <>
          <Typography variant="h4" sx={{ marginTop: 1, mb: 4 }}>
            지원서 질문
          </Typography>
          {questions.map((question, index) => (
            <Box width="100%" key={index} sx={{ marginBottom: 1 }}>
              <Box aria-label="question title" display={'flex'} alignItems={'baseline'}>
                <Typography variant="h6" sx={{ marginBottom: theme.spacing(0.5), textAlign: 'left' }}>
                  {question.contents}
                </Typography>
                {question.isEssential && <Typography variant="caption">&nbsp;*필수항목입니다</Typography>}
              </Box>

              {/* TODO: implement multi-select 
              {question.isCheckBox && question.isMultiSelectable && (
                <Typography variant="caption">&nbsp;다중선택이 가능합니다</Typography>
              )} */}
              {isRecruitingPeriod &&
                (question.isCheckBox ? (
                  <Box style={AnswerFieldStyle}>
                    <FormControl>
                      <RadioGroup name={`${index}`} onChange={e => handleCheckBoxAnswerChange(e, index)}>
                        {question.options?.map((option, choiceIdx) => (
                          <FormControlLabel
                            key={choiceIdx}
                            name={`${index}`}
                            value={option}
                            control={<Radio size="small" />}
                            label={option}
                            disabled={!isRecruitingPeriod}
                          />
                        ))}
                      </RadioGroup>
                    </FormControl>
                  </Box>
                ) : (
                  <>
                    <TextareaAutosize
                      style={AnswerFieldStyle}
                      minRows={4}
                      maxRows={6}
                      name={`${index}`}
                      disabled={!isRecruitingPeriod}
                      maxLength={MAX_LENGTH_PER_ANSWER}
                      onChange={handleTextAnswerChange}
                    />
                    {applyInfo.answers && (
                      <Typography variant="subtitle1" textAlign="right">
                        {applyInfo.answers[index]?.text?.length} / {MAX_LENGTH_PER_ANSWER}
                      </Typography>
                    )}
                  </>
                ))}
            </Box>
          ))}
        </>
      )}
    </>
  )
}

export default ApplyField

const AnswerFieldStyle = {
  padding: 8,
  width: '100%',
  borderRadius: 5,
  border: '1px solid lightgrey',
  fontFamily: 'Pretendard'
}

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />
