import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { CLUB_FORMS_MESSAGE, COMMON_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/club'
import type { Questions } from 'src/models/form'
import type { Recruit, RecruitId } from 'src/models/recruit'

import type { GetQuestionsResponseData } from 'src/responses/question'

export type PutFormRequesData = Pick<Recruit, 'startAt' | 'endAt'>

export type PutFormParams = {
  clubId: ClubId
  recruitId: number
} & PutFormRequesData

interface PutQuestionParams {
  clubId: ClubId
  recruitId: number
  questions: Questions
}

const putQuestionAPICall = async ({ clubId, recruitId, questions }: PutQuestionParams, token: string) => {
  await axios.put<null, AxiosResponse<null>, Questions>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/question`,
    questions,
    {
      headers: { Authorization: token }
    }
  )
}

export const useAddQuestion = (): UseMutationResult<void, unknown, PutQuestionParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PutQuestionParams) => putQuestionAPICall(values, token)

  return useMutation('postQuestion', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(CLUB_FORMS_MESSAGE.ADD_QUESTION_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('questions')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(CLUB_FORMS_MESSAGE.ADD_QUESTION_FAIL, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

interface getQuestionParams {
  clubId: ClubId
  token: string
  recruitId: RecruitId
}

const getQuestionsAPICall =
  ({ clubId, token, recruitId }: getQuestionParams) =>
  async () => {
    const response = await axios.get<GetQuestionsResponseData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/question`,
      {
        headers: { Authorization: token }
      }
    )
    return response.data
  }

export const useGetQuestions = (clubId: ClubId, recruitId: RecruitId) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetQuestionsResponseData>(['questions'], getQuestionsAPICall({ clubId, recruitId, token }), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(error.response?.data?.message || COMMON_MESSAGE.LOAD_FAIL, { variant: 'error' })
    },
    retry: 0
  })
}
